import logo from './logo.svg';
import './App.css';
import AliceCarousel from 'react-alice-carousel';
import "react-alice-carousel/lib/alice-carousel.css";

import image from "./images/image.png"

//I REPLACE IMAGE IN images/image.png
var title = "Download Win11Tools" 
var link = "https://google.com"


// This is template for your downloads. Why using this if your gonna change this.
var btn_text = "Download"
var keys = {37: 1, 38: 1, 39: 1, 40: 1};

function preventDefault(e) {
  e.preventDefault();
}

function preventDefaultForScrollKeys(e) {
  if (keys[e.keyCode]) {
    preventDefault(e);
    return false;
  }
}

// modern Chrome requires { passive: false } when adding event
var supportsPassive = false;
try {
  window.addEventListener("test", null, Object.defineProperty({}, 'passive', {
    get: function () { supportsPassive = true; } 
  }));
} catch(e) {}

var wheelOpt = supportsPassive ? { passive: false } : false;
var wheelEvent = 'onwheel' in document.createElement('div') ? 'wheel' : 'mousewheel';

// call this to Disable
function disableScroll() {
  window.addEventListener('DOMMouseScroll', preventDefault, false); // older FF
  window.addEventListener(wheelEvent, preventDefault, wheelOpt); // modern desktop
  window.addEventListener('touchmove', preventDefault, wheelOpt); // mobile
  window.addEventListener('keydown', preventDefaultForScrollKeys, false);
}
function App() {
  return (
    <div className="h-screen content-center bg-gradient-to-r from-slate-600 to-slate-950 bg-gradient-to-r hover:to-slate-900">
    <div className='grid gap-4 place-content-center h-60'>
    <h1 className='text-7xl text-sky-400 italic font-light	font-serif'>{title}</h1>
    </div>


    <div className='grid place-content-center'> 

    <div className="grid justify-center drop-shadow-4xl rounded-lg p-3 dark:bg-slate-800k bg-opacity-10 bg-slate-200 backdrop-blur-md" style={{width: 760, height:492}}>
      <img src={image}></img>
    </div>
    </div>

    <div className='grid place-content-center p-10'>



    <a href={link}><button class="DLD transition-all duration-700 rounded-full italic text-4xl bg-gradient-to-r from-cyan-500 to-blue-500 hover:scale-110 text-sky-100 h-20 w-80" >{btn_text}</button></a>
    
    </div>
    </div>
  );
}

export default App;
